import { Icon as Iconify } from "@iconify/react";
import { Alert, Card, CardContent, Grid, styled } from "@mui/material";
import DOMPurify from "dompurify";
import Markdown from "marked-react";

import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import Form from "./Form";

const MarkdownContainer = styled("div")(({ theme }) => ({
    "& h3": {
        marginTop: 0,
    },
}));

const Page = () => {
    const GUIDE = `
### نکات آموزشی

با سرویس جایزه مشتریان را شگفت زده و خوشحال کنید.
چند متن مختلف با احتمال های مختلف تعریف کنید ، هر وقت مشتری شماره خودش را وارد دستگاه کند یکی از این متن ها را بصورت تصادفی و بر اساس احتمالی که شما تعریف کرده اید دریافت می کند.

با یک مثال توضیح می دهیم : شما به مشتری می گویید شماره اش را وارد دستگاه کند تا شانس خود برای دریافت تخفیف 50 درصدی را امتحان کند.
سپس چند متن به این صورت تعریف می کنید :
متن :"امیدوارم در خرید بعدی برنده تخفیف ویژه ما باشید " احتمال : 50%
متن :"شما برنده 10 درصد تخفیف شدید" احتمال : 30%
متن :"شما برنده ده هزار تومان تخفیف شدید" احتمال : 15%
متن :"شما برنده 50% تخفیف ویژه ما شدید ، تبریک!!" احتمال : 5%
در مثال بالا 50% مشتری ها پیامکی را دریافت می کنند که نشان می دهد برنده نشده اند ، 30% برنده 10 درصد تخفیف می شوند ، 15% برنده ده هزار تومان تخفیف و فقط 5% مشتریان برنده تخفیف 50% می شوند.
`;

    return (
        <TabView
            title="ایجاد جایزه"
            Icon={(props) => (
                <Iconify {...props} icon="solar:gift-bold-duotone" />
            )}
        >
            <ResponsiveContainer>
                <Card variant="outlined" sx={{ mb: 3 }}>
                    <CardContent sx={{ p: 3 }}>
                        <Grid container spacing={5}>
                            <Grid item xs={12} sm={6}>
                                <Alert color="info">
                                    <MarkdownContainer>
                                        <Markdown breaks={true}>
                                            {DOMPurify.sanitize(GUIDE)}
                                        </Markdown>
                                    </MarkdownContainer>
                                </Alert>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <Form />
                            </Grid>
                        </Grid>
                    </CardContent>
                </Card>
            </ResponsiveContainer>
        </TabView>
    );
};

export default Page;
