import { Icon } from "@iconify/react";
import {
	Alert,
	AlertTitle,
	Box,
	Button,
	Chip,
	Collapse,
	Divider,
	Grid,
	InputAdornment,
	ListItem,
	ListItemButton,
	ListItemIcon,
	ListItemText,
	Switch,
	Typography,
} from "@mui/material";
import { filter, get, map, set, some, sumBy } from "lodash";
import { useSnackbar } from "notistack";
import { useTranslation } from "react-i18next";
import { useMiddleware } from "../../../../Application";
import TextField from "../../../../components/TextField";
import useForm from "../../../../utils/useForm";
import SmsField from "../../components/SmsField";
import { useEffect, useState } from "react";

const Form = () => {
	const { t } = useTranslation(["customers-club", "global"]);

	const { enqueueSnackbar } = useSnackbar();

	const middleware = useMiddleware("Customers-Club.Rewards");

	const statusVariants = {
		pending: {
			type: "pending",
			color: "warning",
			label: t("global:general.pending"),
			text: t("pages.welcome.welcome_pending_text"),
		},
		confirmed: {
			type: "confirmed",
			color: "success",
			label: t("global:general.active"),
			text: t("pages.welcome.revisit_active_text"),
		},
		rejected: {
			type: "rejected",
			color: "error",
			label: t("global:general.inactive"),
			text: t("pages.welcome.welcome_inactive_text"),
		},
	};
	const getStatusVariant = (type) => statusVariants[type];

	const { register, setValue, getValue, reset, loading, handleSubmit } = useForm({
		initialValues: {
			rewards: [],
			active: false,
		},
		handlePost: (data) => middleware.createOrUpdate(data),
		onSubmit: (response) => {
			enqueueSnackbar(
				t("global:general.update_successful", "Update successful")
			);
			// setInitialValues(response.options);
		},
	});

	const getOptions = async () => {
		const response = await middleware.getOptions();
		reset(response.data);
	};

	useEffect(() => {
		getOptions();
	}, []);

	const handleAddReward = () => {
		const total = sumBy(getValue("rewards"), reward => Number(reward.possiblity));

		if (total === 100) {
			enqueueSnackbar(t("احتمال نمیتواند بیشتر از 100 باشد"));
			return;
		}

		setValue("rewards", [
			...getValue("rewards"),
			{
				possiblity: 100 - total,
				message: {
					content: "",
				},
			},
		]);
	};

	const getRewardValue = (index, key, defaultValue) => {
		return get(getValue("rewards"), `[${index}][${key}]`, defaultValue);
	};

	const setRewardValue = (index, key, value) => {
		setValue(
			"rewards",
			map(getValue("rewards"), (reward, idx) =>
				idx === index ? set(reward, key, value) : reward
			)
		);
	};

	return (
		<Box>
			<Box sx={{ mb: 2 }}>
				{!getValue("rewards").length && (
					<Alert severity="warning" icon={false} sx={{ mb: 2 }}>
						<Typography>جایزه‌ای وجود ندارد</Typography>
					</Alert>
				)}

				{getValue("rewards").length > 0 &&
					getValue("rewards").map((reward, index) => {
						const status = reward?.message?.status ? getStatusVariant(reward?.message?.status) : {};

						return (
							<>
								<Collapse in={["pending", "rejected"].includes(status.type)}>
									<Alert
										icon={false}
										variant="standard"
										severity={status?.color}
										action={
											status?.type === "pending" && (
												<Chip color={status?.color} label={status?.label} />
											)
										}
										sx={{ mb: 2 }}
									>
										<AlertTitle>{t("global:general.status")}</AlertTitle>
										{status?.text}
									</Alert>
								</Collapse>
								<Grid
									container
									spacing={2}
									alignItems="center"
									key={index}
								>
									<Grid item xs={10}>
										<TextField
											type="number"
											min={1}
											max={100}
											size="small"
											label={t("احتمال")}
											value={getRewardValue(
												index,
												"possiblity"
											)}
											onChange={(e) =>
												setRewardValue(
													index,
													"possiblity",
													e.target.value
												)
											}
											InputProps={{
												endAdornment: (
													<InputAdornment position="end">
														درصد
													</InputAdornment>
												)
											}}
										/>
									</Grid>
	
									<Grid item xs={2}>
										<Button
											size="small"
											color="error"
											variant="contained"
											onClick={() =>
												setValue(
													"rewards",
													filter(
														getValue("rewards"),
														(reward, idx) =>
															idx !== index
													)
												)
											}
										>
											حذف
										</Button>
									</Grid>
	
									<Grid item xs={12}>
										<SmsField
											placeholder={t("متن پیام...")}
											value={getRewardValue(
												index,
												"message.content"
											)}
											onChange={(value) =>
												setRewardValue(
													index,
													"message.content",
													value
												)
											}
										/>
									</Grid>
								</Grid>
								<Divider sx={{ my: 3 }} />
							</>
						);
					})}

				<Button
					size="small"
					color="success"
					variant="contained"
					onClick={handleAddReward}
					startIcon={<Icon icon="solar:add-linear" />}
				>
					افزودن جایزه
				</Button>

				<Box sx={{ my: 2 }} />

				<ListItem disableGutters disablePadding>
					<ListItemButton
						selected
						disabled={some(getValue("rewards"), reward => reward.message?.status !== "confirmed")}
						onClick={() => setValue("active", !getValue("active"))}
					>
						<ListItemText
							primary={t("ارسال فعال")}
							secondary={t(
								"در صورت غیر فعال بودن، پیام ارسال نخواهد شد."
							)}
						/>
						<ListItemIcon>
							<Switch
								checked={getValue("active")}
								onChange={(e) =>
									setValue("active", e.target.checked)
								}
							/>
						</ListItemIcon>
					</ListItemButton>
				</ListItem>
			</Box>

			<Box sx={{ my: 3 }} />

			<Button
				variant="contained"
				onClick={handleSubmit}
				disabled={loading || (getValue("rewards").length > 0 && sumBy(getValue("rewards"), reward => Number(reward.possiblity)) < 100)}
			>
				{t("global:general.save_changes")}
			</Button>
		</Box>
	);
};

export default Form;
